import { Component, OnInit } from '@angular/core';
import { ClientService } from '../../services/client.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-users',
  templateUrl: './users.component.html',
  styleUrls: ['./users.component.scss']
})
export class UsersComponent implements OnInit {
  public users = [];
  public trs = '';
  public trsExcel = '';
  constructor( private router: Router, private clientService: ClientService ) { }

  
  ngOnInit(): void {
    this.clientService.obtenerUsuarios().subscribe(
      data => {
        this.users = data['data']['winners'];
        if( this.users.length > 0 ) {
          this.users.forEach(user=>{
            var current = user['currentModule'] == 6 ? 'Finaliz todos los mdulos' : 'En proceso';
            var initTrExcel = 
            `<tr>
               <th scope="row">${user['position']}</th>
               <td>${user['nameComplete']}</a></td>
               <td>${user['email']}</td>
               <td>${user['city']}</td>
               <td>${user['educationLevel']}</td>
               <td>${user['age']}</td>
               <td>${current}</td>
            </tr>`;
            this.trsExcel = this.trsExcel + initTrExcel;
          });
        }
      },
      err => {
        this.router.navigateByUrl('/inicio');
      }
    );
  }

  exportarTablaExcel( data ) {
    var htmls = "";
    var uri = 'data:application/vnd.ms-excel;base64,';
    var template = '<html xmlns:o="urn:schemas-microsoft-com:office:office" xmlns:x="urn:schemas-microsoft-com:office:excel" xmlns="http://www.w3.org/TR/REC-html40"><meta http-equiv="content-type" content="application/vnd.ms-excel; charset=UTF-8"><head><!--[if gte mso 9]><xml><x:ExcelWorkbook><x:ExcelWorksheets><x:ExcelWorksheet><x:Name>{worksheet}</x:Name><x:WorksheetOptions><x:DisplayGridlines/></x:WorksheetOptions></x:ExcelWorksheet></x:ExcelWorksheets></x:ExcelWorkbook></xml><![endif]--></head><body><table>{table}</table></body></html>'; 
    var base64 = function(s) {
      return window.btoa(unescape(encodeURIComponent(s)))
    };
    var format = function(s, c) {
      return s.replace(/{(\w+)}/g, function(m, p) {
        return c[p];
      })
    };
    htmls = 
      ` <thead>
            <tr>
              <th>#</th>
              <th>Nombre Completo</th>
              <th>Email</th>
              <th>Ciudad</th>
              <th>Educacin</th>
              <th>Edad</th>
              <th>Estado</th>
            </tr>
          </thead>
          <tbody>
          ${this.trsExcel}
          </tbody>`;
    var ctx = {
      worksheet : 'Worksheet',
      table : htmls
    }
    var link = document.createElement("a");
    var now = new Date();
    var date = now.getFullYear() + '-' + now.getMonth() + '-' + now.getDate();
    link.download = "usuarios-" + date + ".xls";
    link.href = uri + base64(format(template, ctx));
    link.click();
  }

  verUsuario(user) {
    this.router.navigateByUrl('/admin/usuarios/datos-personales', { state: user });
  }

}
