import { Component, OnInit } from '@angular/core';
import { SwiperOptions } from 'swiper';
import { NgwWowService } from 'ngx-wow';
import { Router } from '@angular/router';
@Component({
  selector: 'app-chapter3',
  templateUrl: './chapter3.component.html',
  styleUrls: ['./chapter3.component.scss']
})
export class Chapter3Component implements OnInit {
  public finalModule;
  config: SwiperOptions = {
    autoHeight: true,
    allowTouchMove: false,
    mousewheel: false,
    direction: 'horizontal',
    slidesPerView: 1,
    effect: 'coverflow',
    pagination: { el: '.swiper-pagination', type: 'custom', clickable: false },
    navigation: {
      nextEl: '.swiper_next',
      prevEl: '.swiper_prev'
    },
    fadeEffect: {
      crossFade: true
    },
    spaceBetween: 0
  };

  constructor(
    private wowService: NgwWowService,
    private router: Router
  ) {
    this.wowService.init();
  }

  dismissOne=  true;
  dismissTwo=  false;
  dismissThree=  false;
  dismissFour=  false;
  dismissFive=  false;

  ngOnInit(): void {
    if( localStorage.getItem('email') == null ) {
      this.router.navigateByUrl('/inicio');
    }
    this.finalModule = localStorage.getItem('finish-3') == 'final' ? true : false;
    if ( this.finalModule == false ) {
      localStorage.setItem('module', '3' );
    }
  }
  dismissAlert(){
    this.dismissOne = false;
    this.dismissTwo = true;
  }
  dismissAlert2(){
    this.dismissTwo = false;
    this.dismissThree = true;
  }
  dismissAlert3(){
    this.dismissThree = false;
    this.dismissFour = true;
  }
  dismissAlert4(){
    this.dismissFour = false;
    this.dismissFive = true;
  }
  dismissAlert5(){
    this.dismissFive = false;
  }
  activeWow() {
    this.wowService.init();
  }

}
