import { Component, OnInit } from '@angular/core';
import { ClientService } from '../../services/client.service';
import { Router } from '@angular/router';
import { Client} from '../../models/client.model';
@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.scss']
})
export class UserComponent implements OnInit {
  public users = [];
  public client = new Client('', '');
  public modulesQuiz;
  public birthDate;
  user;
  constructor( private router: Router, private clientService: ClientService ) { 
  }

  ngOnInit(): void {
    this.user = history.state;
    this.client.nameComplete = this.user.nameComplete;
    this.client.gender = this.user.gender;
    this.client.birthdate = this.user.birthdate;
    this.client.age = this.user.age;
    this.client.department = this.user.department;
    this.client.city = this.user.city;
    this.client.address = this.user.address;
    this.client.email = this.user.email;
    this.client.cellPhone = this.user.cellPhone;
    this.client.phone = this.user.phone;
    this.client.educationLevel = this.user.educationLevel;
    this.client.disability = this.user.disability;
    this.client.referenceName = this.user.referenceName;
    this.client.referencePhone = this.user.referenceCellphone;
    this.client.finishedModules = this.user.finishedModules;
    this.client.typeDisability = this.user.typeDisability;
    if( typeof this.user.birthdate == 'undefined' ) {
      this.birthDate = '-.-';
    } else {
      this.birthDate = ( this.user.birthdate ).substring( 0, 10 );
    }
    var quizes = {
      1 : { subtitle : 'Modulo Pendiente', coupon : '', status : 'pendiente'},
      2 : { subtitle : 'Modulo Pendiente', coupon : '', status : 'pendiente'},
      3 : { subtitle : 'Modulo Pendiente', coupon : '', status : 'pendiente'},
      4 : { subtitle : 'Modulo Pendiente', coupon : '', status : 'pendiente'},
      5 : { subtitle : 'Modulo Pendiente', coupon : '', status : 'pendiente'},
    };      
    if( typeof this.client.finishedModules != 'undefined' ) {
      this.user.finishedModules.forEach(function (e) {
        quizes[e['moduleNumber']].coupon = e['coupon'];
        quizes[e['moduleNumber']].subtitle = 'Mdulo completado';
        quizes[e['moduleNumber']].status = 'completado';
      });  
    }
    this.modulesQuiz = quizes;
  }

}
