import { Component, OnInit } from '@angular/core';
import { SwiperOptions } from 'swiper';
import { Router } from '@angular/router';
import { NgwWowService } from 'ngx-wow';

declare function stopVideo();

@Component({
  selector: 'app-chapter4',
  templateUrl: './chapter4.component.html',
  styleUrls: ['./chapter4.component.scss']
})
export class Chapter4Component implements OnInit {
  public finalModule;
  config: SwiperOptions = {
    autoHeight: true,
    allowTouchMove: false,
    mousewheel: false,
    direction: 'horizontal',
    slidesPerView: 1,
    effect: 'coverflow',
    pagination: { el: '.swiper-pagination', type: 'custom', clickable: false },
    navigation: {
      nextEl: '.swiper_next',
      prevEl: '.swiper_prev'
    },
    fadeEffect: {
      crossFade: true
    },
    spaceBetween: 0
  };

  constructor(
    private wowService: NgwWowService,
    private router: Router
  ) {
    this.wowService.init();
  }

  dismissOne=  true;
  dismissTwo=  false;
  dismissThree=  false;
  dismissFour=  true;
  dismissFive=  false;
  dismissSix=  true;
  dismissSeven= true;
  dismissEight= false;
  dismissNine= false;

  ngOnInit(): void {
    if( localStorage.getItem('email') == null ) {
      this.router.navigateByUrl('/inicio');
    }
    this.finalModule = localStorage.getItem('finish-4') == 'final' ? true : false;
    if( this.finalModule == false ) {
      localStorage.setItem('module', '4' );
    }
  }
  dismissAlert(){
    this.dismissOne = false;
    this.dismissTwo = true;
    this.dismissFive=  true;
  }
  dismissAlert2(){
    this.dismissTwo = false;
    this.dismissThree = true;
    this.dismissFive=  false;
  }
  dismissAlert3(){
    this.dismissThree = false;
  }
  dismissAlert4(){
    this.dismissFour = false;
    this.dismissFive = true;
  }
  dismissAlert5(){
    this.dismissSix = false;
  }
  dismissAlert6(){
    this.dismissSeven = false;
    this.dismissEight = true
  }
  dismissAlert7(){
    this.dismissEight = false
    this.dismissNine= true;
  }
  dismissAlert8(){
    this.dismissNine= false;
  }
  activeWow() {
    this.wowService.init();
    this.dismissOne = true;
    this.dismissTwo = false;
    this.dismissThree = false;
    stopVideo();
  }

}
