import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { SwiperOptions } from 'swiper';
import { NgwWowService } from 'ngx-wow';

declare function stopAudio();

@Component({
  selector: 'app-chapter5',
  templateUrl: './chapter5.component.html',
  styleUrls: ['./chapter5.component.scss']
})

export class Chapter5Component implements OnInit {
  public finalModule;
  config: SwiperOptions = {
    autoHeight: true,
    allowTouchMove: false,
    mousewheel: false,
    direction: 'horizontal',
    slidesPerView: 1,
    effect: 'coverflow',
    pagination: { el: '.swiper-pagination', type: 'custom', clickable: false },
    navigation: {
      nextEl: '.swiper_next',
      prevEl: '.swiper_prev'
    },
    fadeEffect: {
      crossFade: true
    },
    spaceBetween: 0
  };

  constructor(
    private wowService: NgwWowService,
    private router: Router
    ) {
      this.wowService.init();
  }
    
  dismissOne=  true;
  dismissTwo=  false;
  dismissThree=  false;
  dismissFour=  false;
  dismissFive=  false;
  dismissSix=  false;
  dismissSeven=  false;

  ngOnInit(): void {
    if( localStorage.getItem('email') == null ) {
      this.router.navigateByUrl('/inicio');
    }
    this.finalModule = localStorage.getItem('finish-5') == 'final' ? true : false;
    if ( this.finalModule == false ) {
      localStorage.setItem('module', '5' );
    }

    stopAudio();
  }
  dismissAlert(){
    this.dismissOne = false;
    this.dismissTwo = true;
  }
  dismissAlert2(){
    this.dismissTwo = false;
    this.dismissThree = true;
  }
  dismissAlert3(){
    this.dismissThree = false;
    this.dismissFour = true;
  }
  dismissAlert4(){
    this.dismissFour = false;
    this.dismissFive = true;
  }
  dismissAlert5(){
    this.dismissFive = false;
    this.dismissSix = true;
  }
  dismissAlert6(){
    this.dismissSix = false;
    this.dismissSeven = true;
  }
  dismissAlert7(){
    this.dismissSeven = false;
  }
  activeWow() {
    this.wowService.init();
    this.dismissOne = true;
    this.dismissTwo = false;
    this.dismissThree = false;
    this.dismissFour = false;
    this.dismissFive = false;
    this.dismissSix = false;
    this.dismissSeven = false;
  }
}
