import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ClientService } from '../../services/client.service';
import { Client} from '../../models/client.model';
import { Router } from '@angular/router';
import { FormArray } from '@angular/forms';
import { forkJoin, from } from 'rxjs';
import { concatMap } from 'rxjs/operators';
@Component({
  selector: 'app-quiz4',
  templateUrl: './quiz4.component.html',
  styleUrls: ['./quiz4.component.scss']
})
export class Quiz4Component implements OnInit {

  public isAuth: boolean = false;
  public contentUser = {};
  public answerQuestions = {};
  public client = new Client('', '');
  public questions = [];
  public modulefinished = false;
  public numberModule = 4;
  public itsAllOkay = false;
  public completeAllQuiz = false;

  public quiz4Form = this.fb.group({
    question1: '',
    question2: '',
  },
    {}
  );  
  


  constructor(
    private router: Router,
    private fb: FormBuilder,
    private clientService: ClientService
  ) { 
    
  }

  ngOnInit(): void {
    var now = new Date();
    if( now.getTime() > Date.parse( localStorage.getItem('date_expiration') ) && localStorage.getItem('date_expiration') != null ) {
      const requests = [
        this.clientService.refrescarToken(),
        // this.clientService.obtenerUsuario()
      ];
      from(requests).pipe(    
        concatMap((request) => request.pipe())
      ).subscribe((data) => {
        this.clientService.obtenerUsuario().subscribe(
          res => {
            if( res == false ) {
              this.router.navigateByUrl('/inicio');
              return;
            }
            this.contentUser = res['data'];
            this.client.address = this.contentUser['address'];
            this.client.age = this.contentUser['age'];
            this.client.cellPhone = this.contentUser['cellPhone'];
            this.client.ci = this.contentUser['ci'];
            this.client.city = this.contentUser['city'];
            this.client.completeRegister = this.contentUser['completeRegister'];
            this.client.currentModule = this.contentUser['currentModule'];
            this.client.disability = this.contentUser['disability'];
            this.client.educationLevel = this.contentUser['educationLevel'];
            this.client.email = this.contentUser['email'];
            this.client.gender = this.contentUser['gender'];
            this.client.nombre = this.contentUser['nameComplete'];
            this.client.nameComplete = this.contentUser['nameComplete'];
            this.client.phone = this.contentUser['phone'];
            this.client.role = this.contentUser['role'];        
            this.isAuth = true;
            if( typeof this.contentUser['finishedModules'] != 'undefined' ) {
              var existModule = this.contentUser['finishedModules'].filter( elem => {
                return elem.moduleNumber == this.numberModule;
              });
              if( existModule.length > 0 ) {
                this.router.navigateByUrl('/inicio');
              }  
            }
          }
        );
      });      
    } else {
      if( localStorage.getItem('email') != null ) {
        this.clientService.obtenerUsuario().subscribe(
          data => {
            if( data == false ) {
              this.router.navigateByUrl('/inicio');
              return;
            }        
            this.isAuth = true;
            this.contentUser = data['data'];
            this.client.address = this.contentUser['address'];
            this.client.age = this.contentUser['age'];
            this.client.cellPhone = this.contentUser['cellPhone'];
            this.client.ci = this.contentUser['ci'];
            this.client.city = this.contentUser['city'];
            this.client.completeRegister = this.contentUser['completeRegister'];
            this.client.currentModule = this.contentUser['currentModule'];
            this.client.disability = this.contentUser['disability'];
            this.client.educationLevel = this.contentUser['educationLevel'];
            this.client.email = this.contentUser['email'];
            this.client.gender = this.contentUser['gender'];
            this.client.nombre = this.contentUser['nameComplete'];
            this.client.nameComplete = this.contentUser['nameComplete'];
            this.client.phone = this.contentUser['phone'];
            this.client.role = this.contentUser['role'];
            if( typeof this.contentUser['finishedModules'] != 'undefined' ) {
              var existModule = this.contentUser['finishedModules'].filter( elem => {
                return elem.moduleNumber == this.numberModule;
              });
              if( existModule.length > 0 ) {
                this.router.navigateByUrl('/inicio');
              }  
            }
          },
          err => {
            this.router.navigateByUrl('/inicio');
          }
        );        
      }
    }
    

    this.clientService.obtenerQuiz( this.numberModule ).subscribe(
      data => {
        this.questions = data['data'].questions;

          // Uniendo en un solo objeto las respuestas para poder manejar la data fcil
        this.questions.forEach(question => {
          question.answers.forEach(answer => {
            this.answerQuestions[answer.idAnswer] = answer.state;
          });
        });
      },
      err => {
        this.router.navigateByUrl('/inicio');
      }
    );
  }

  validarNumChecks( question, answer ) {
    var limite=2; //limite de checks a seleccionar
    var num=0;
    let checked = document.querySelectorAll(`input[name=check-question${question}]:checked`);
    let answerInput = document.getElementById(`answer-${answer}`) as HTMLInputElement;
    if( checked.length > limite ) {
      answerInput.checked = false;
    }
  }

  obtenerSeleccionados( inputName ) {
    var allOkey = true;
    let chks = document.querySelectorAll<HTMLInputElement>(`input[name=${inputName}]`);
    for (var i = 0; i < chks.length; i++) {
      var icon = document.createElement('i');
      if (chks[i].checked ) {
        if( this.answerQuestions[chks[i].value]  ) {
          icon.setAttribute('class', 'delete-this-icon fa fa-check-circle color-succes ml-2');
          icon.setAttribute('aria-hidden', 'true');
          icon.setAttribute('checked', 'true');
        } else {
          allOkey = false;
          icon.setAttribute('class', 'delete-this-icon fa fa-times-circle color-error ml-2');
          icon.setAttribute('aria-hidden', 'true');
        }
      }
      var app = document.querySelector('#check-answer-' + chks[i].value );
      app.append( icon );
    }
    return allOkey;
  }

  simulateClick(elem) {
    var evt = new MouseEvent('click', {
      bubbles: true,
      cancelable: true,
      view: window
    });
    var canceled = !elem.dispatchEvent(evt);
  };   

  completarQuiz4( dataQuiz ) {
    let emptyMessage = document.querySelector('#empty-quiz' );
    emptyMessage.setAttribute( 'style', 'display:none;' );
    if( this.verificarVacio( dataQuiz ) && this.itsAllOkay == false ) {
      emptyMessage.setAttribute( 'style', 'display:block' );
      return;
    }
    if( this.verificarRespuestas( dataQuiz ) == false ) {
      return;
    };
    this.itsAllOkay = true; 
    // MODAL QUE INDICA SE FINALIZ TODOS LOS MDULOS
    let btnFinishModuleQuiz = document.querySelector('#modal-quiz-5');
    btnFinishModuleQuiz.setAttribute('data-toggle', 'modal');
    btnFinishModuleQuiz.setAttribute('data-target', '.certificate-completed');

    // MODAL QUE INDICA QUE SOLO SE MODIFIC EL MDULO ACTUAL
    let btnQuiz = document.querySelector('#btn-quiz-' + this.numberModule);
    let btnClose       = document.querySelector('#modal-close-' + this.numberModule);
    let btnCloseFinish = document.querySelector('#close-modal-finish');    
    btnQuiz.setAttribute('data-toggle', 'modal');
    btnQuiz.setAttribute('data-target', '.module-completed');

    if( this.completeAllQuiz ) {
      this.simulateClick(btnFinishModuleQuiz);
      return;
    } else if( this.modulefinished ) {
      this.simulateClick(btnQuiz);
      return;
    }
    var now = new Date();
    var bntFinal;
    if( now.getTime() > Date.parse( localStorage.getItem('date_expiration') ) && localStorage.getItem('date_expiration') != null ) {
      const requests = [
        this.clientService.refrescarToken(),
      ];
      from(requests).pipe(    
        concatMap((request) => request.pipe())
      ).subscribe((data) => {
        this.clientService.guardarQuiz(this.numberModule).subscribe(
          res => {
            this.modulefinished = true;
            localStorage.removeItem( 'module' );
            if ( ( this.contentUser['finishedModules'].length + 1 ) == 5 ) {
              this.completeAllQuiz = true;
              this.simulateClick(btnFinishModuleQuiz);
              bntFinal = btnCloseFinish;
            } else {
              this.simulateClick(btnQuiz);  
              bntFinal = btnClose;    
            }
            setTimeout(() => {
              this.simulateClick(bntFinal);
              this.router.navigateByUrl('/inicio');
            }, 5000);
          },
          err => {
            alert( err.error.message );
          }
        )
      });
    } else {
      this.clientService.guardarQuiz( this.numberModule ).subscribe(
        data => {
          this.modulefinished = true;
          localStorage.removeItem( 'module' );
          if ( ( this.contentUser['finishedModules'].length + 1 ) == 5 ) {
            this.completeAllQuiz = true;
            this.simulateClick(btnFinishModuleQuiz);
            bntFinal = btnCloseFinish;
          } else {
            this.simulateClick(btnQuiz);
            bntFinal = btnClose;
          }
          setTimeout(() => {
            this.simulateClick(bntFinal);
            this.router.navigateByUrl('/inicio');
          }, 5000);
        },
        err => {
          alert( err.error.message );
        }
      );  
    }
    
  }

  verificarRespuestas( dataQuiz ) {
    var allOkey = true;
      // Limpiando los checks al crearlos
    var el = document.getElementsByClassName("delete-this-icon");
    while (el.length > 0) el[0].remove();
      // Verificando Si la respuesta es correcta o no para agregar el 'icono
    for( let element in dataQuiz ) {
      var icon = document.createElement('i');
      if( typeof dataQuiz[element] == 'boolean' ) {
        if( this.obtenerSeleccionados( 'check-' + element ) == false ) {
          allOkey = false;
        }
      } else {
        if( this.answerQuestions[dataQuiz[element]] ) {
          icon.setAttribute('class', 'delete-this-icon fa fa-check-circle color-succes ml-2');
          icon.setAttribute('aria-hidden', 'true');
        } else {
          allOkey = false;
          icon.setAttribute('class', 'delete-this-icon fa fa-times-circle color-error ml-2');
          icon.setAttribute('aria-hidden', 'true');
        }
        var app = document.querySelector('#check-answer-' + dataQuiz[element] );
        app.append( icon );
      }
    }
    return allOkey;
  }

  verificarVacio( dataQuiz ) {    
    for( let element in dataQuiz ) {
      if( dataQuiz[element] == '' || dataQuiz[element] == false ) {
        return true;
      }
    }
    return false;
  }
}
