import { Component, OnInit } from '@angular/core';

import { SwiperOptions } from 'swiper';
import { NgwWowService } from 'ngx-wow';
import { Router } from '@angular/router';
declare function stopVideo();

@Component({
  selector: 'app-chapter1',
  templateUrl: './chapter1.component.html',
  styleUrls: ['./chapter1.component.scss']
})
export class Chapter1Component implements OnInit {
  public finalModule;
  constructor( private wowService: NgwWowService, private router: Router) {
    this.wowService.init();
  }

  config: SwiperOptions = {
    mousewheel: false,
    cssMode: false,
    allowTouchMove: false,
    autoHeight: true,
    slidesPerView: 1,
    effect: 'coverflow',
    navigation: {
      nextEl: '.swiper_next',
      prevEl: '.swiper_prev'
    },
  };

  ngOnInit(): void {
    if( localStorage.getItem('email') == null ) {
      this.router.navigateByUrl('/inicio');
    }
    this.finalModule = localStorage.getItem('finish-1') == 'final' ? true : false;
  }

  activeWow() {
    this.wowService.init();
    stopVideo();
  }
  stopSecuence() {
    stopVideo();
  }
}
