import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ClientService } from '../../services/client.service';
import { Client} from '../../models/client.model';
import { Router } from '@angular/router';
import { forkJoin, from } from 'rxjs';
import { concatMap } from 'rxjs/operators';
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {

  public formSubmitted = false;
  public client = new Client( '', '', '');
  public isAuth: boolean = false;
  public contentUser = {};
  public classCurrentModule = '';
  public openModal = true;
  public finishedModules = {};
  public textInitSession = 'Iniciando sesin...';

  // public usuario : Client;

  public PreRegisterForm = this.fb.group({
    email: ['', [Validators.required, Validators.email]],
    ci: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(8)]],
    password: ['', Validators.required],
    confirmPassword: ['', Validators.required],
    acceptTerms: [true, Validators.required],
  },
    {
      validators: this.passwordsIguales('password', 'confirmPassword')
  });

  public loginForm = this.fb.group({
    email: [ localStorage.getItem('email') || '' , [ Validators.required, Validators.email ] ],
    password: ['', Validators.required ],
    remember: [false]
  });

  public accountRecuperation = this.fb.group({
    emailRecuperation: ['', [Validators.required]],
  },
    {}
  );  

  constructor(
    private router: Router,
    private fb: FormBuilder,
    private clientService: ClientService,
    private spinner: NgxSpinnerService
  ) { }

  ngOnInit(): void {
    var now = new Date();
    // 
    var isTrue = true;
    if( now.getTime() > Date.parse( localStorage.getItem('date_expiration') ) && localStorage.getItem('date_expiration') != null ) {
      const requests = [
        this.clientService.refrescarToken()
      ];
      from(requests).pipe(    
        concatMap((request) => request.pipe())
      ).subscribe((data) => {
        this.clientService.obtenerUsuario().subscribe(
          res => {
            if( res == false ) {
              // alert( 'Existen errores al procesar su solicitud.' );
              this.router.navigateByUrl('/inicio');
              return;
            }
            this.contentUser = res['data'];
            this.client.address = this.contentUser['address'];
            this.client.age = this.contentUser['age'];
            this.client.cellPhone = this.contentUser['cellPhone'];
            this.client.ci = this.contentUser['ci'];
            this.client.city = this.contentUser['city'];
            this.client.completeRegister = this.contentUser['completeRegister'];
            this.client.currentModule = this.contentUser['currentModule'];
            this.client.disability = this.contentUser['disability'];
            this.client.educationLevel = this.contentUser['educationLevel'];
            this.client.email = this.contentUser['email'];
            this.client.gender = this.contentUser['gender'];
            this.client.nombre = this.contentUser['nameComplete'];
            this.client.nameComplete = this.contentUser['nameComplete'];
            this.client.phone = this.contentUser['phone'];
            this.client.role = this.contentUser['role'];        
            this.isAuth = true;
          }
        )
      });
    } else {
      if( localStorage.getItem('email') != null ) {
        this.clientService.obtenerUsuario().subscribe(
          data => {
            if( data == false ) {
              this.router.navigateByUrl('/inicio');
              return;
            }
            this.contentUser = data['data'];
            this.isAuth = true;
            this.client.address = this.contentUser['address'];
            this.client.age = this.contentUser['age'];
            this.client.cellPhone = this.contentUser['cellPhone'];
            this.client.ci = this.contentUser['ci'];
            this.client.city = this.contentUser['city'];
            this.client.completeRegister = this.contentUser['completeRegister'];
            this.client.currentModule = this.contentUser['currentModule'];
            this.client.disability = this.contentUser['disability'];
            this.client.educationLevel = this.contentUser['educationLevel'];
            this.client.email = this.contentUser['email'];
            this.client.gender = this.contentUser['gender'];
            this.client.nombre = this.contentUser['nameComplete'];
            this.client.nameComplete = this.contentUser['nameComplete'];
            this.client.phone = this.contentUser['phone'];
            this.client.role = this.contentUser['role'];
          },
          err => {
            this.router.navigateByUrl('/inicio');
            // this.contentUser = JSON.parse(err.error).message;
          }
        );
      }
    }
  }

  crearUsuario() {
    var btnPreregisterModal      = document.getElementById('preregister-modal') as HTMLElement;
    var btnPreregisterModalClose = document.getElementById('preregister-modal-close') as HTMLElement;
    this.formSubmitted = true;
    if (this.PreRegisterForm.invalid) {
      return;
    }
    this.textInitSession = 'Creando cuenta...';
    this.spinner.show();
    this.clientService.crearUsuario(this.PreRegisterForm.value)
    .subscribe( resp => {
      setTimeout(() => {
        btnPreregisterModalClose.click();
        btnPreregisterModal.click();    
        this.spinner.hide();
      }, 2000);
        // this.router.navigateByUrl('/inicio');
      // location.reload(); 
    }, (err)=> {
      setTimeout(() => {
        this.spinner.hide();
        alert(err.error.message);
      }, 2000);
      return;
    });
  }

  campoNoValido(campo: string): boolean {
    if (this.PreRegisterForm.get(campo).invalid && this.formSubmitted) {
      return true;
    } else {
      return false;
    }
  }

  contrasenasInvalidas() {
    const pass1 = this.PreRegisterForm.get('password').value;
    const pass2 = this.PreRegisterForm.get('confirmPassword').value;

    if ((pass1 != pass2) && this.formSubmitted) {
      return true;
    } else {
      return false;
    }
  }

  passwordsIguales(pass1Name: string, pass2Name: string) {
    return (formGroup: FormGroup)=> {
      const pass1Control = formGroup.get(pass1Name);
      const pass2Control = formGroup.get(pass2Name);
      if (pass1Control.value === pass2Control.value) {
        pass2Control.setErrors(null)
      } else {
        pass2Control.setErrors({ noEsigual: true })
      }
    }
  }

  login() {
    this.clientService.login( this.loginForm.value )
      .subscribe( resp => {
       if ( this.loginForm.get('remember').value ){ 
          localStorage.setItem('email', this.loginForm.get('email').value );
        } else {
          localStorage.removeItem('email');
          location.reload(); 
        }
        // Navegar al Dashboard
        this.router.navigateByUrl('/admin');
        location.reload(); 

      }, (err) => {
        alert( err.error.message );
      });
  }

  loginTwo() {
    this.textInitSession = 'Iniciando sesin...';
    this.spinner.show();
    this.clientService.login( this.loginForm.value )
      .subscribe( resp => {
        setTimeout(() => {
          this.spinner.hide();
          if( typeof resp.error != 'undefined' ) {
            alert( resp.error['message'] );
          } else{
            var btnClose = document.getElementById('btn-close') as HTMLElement;
            btnClose.click();
            this.router.navigateByUrl('/registro');
          }
        }, 1000);
        
      }, (err) => {
        setTimeout(() => {
          this.spinner.hide();
          alert( err.error.message );
        }, 1000)
      });
  }

  logout() {
    this.textInitSession = 'Cerrando sesin...';
    this.spinner.show();
    var lastWordUrl = (document.location.href).slice(-6);
    setTimeout(() => {
      localStorage.removeItem( 'email' );
      localStorage.removeItem( 'date_expiration' );
      localStorage.removeItem( 'token' );
      localStorage.removeItem('module');
      localStorage.removeItem('finish-1');
      localStorage.removeItem('finish-2');
      localStorage.removeItem('finish-3');
      localStorage.removeItem('finish-4');
      localStorage.removeItem('finish-5');
      this.router.navigateByUrl('/inicio');
      this.spinner.hide();
      if( lastWordUrl == 'inicio' ) {
        location.reload();
      }
    }, 1500);
  }

  recuperarCuenta( data ) {
    this.textInitSession = 'Recuperando cuenta...';
    this.spinner.show();
    this.clientService.recuperarCuenta( data['emailRecuperation'] )
      .subscribe( resp => {
        setTimeout(() => {
          this.spinner.hide();
          alert( resp['message'] ); 
          this.router.navigateByUrl('/inicio');
          location.reload();
        });
        // 
      }, (err) => {
        alert( err );
    });
  }

  clearData() {
    this.loginForm.reset();
  }

}
