import { Injectable, NgZone } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { tap, map, catchError } from 'rxjs/operators';
import { Router } from '@angular/router';
import { BehaviorSubject, Observable, of } from 'rxjs';
import { environment } from '../../environments/environment';


import { PreRegisterForm } from '../interfaces/preregister-form.interface';
import { LoginForm } from '../interfaces/login-form.interface';
import { Client } from '../models/client.model';
import { RegisterForm } from '../interfaces/register-form.interface';

const base_url     = environment.base_url;
const publicToken  = environment.publicToken;
const appUserId    = environment.appUserId;
const authorizationBasic = environment.authorizationBasic;
const userAplication     = environment.userAplication;
const passwordAplication = environment.passwordAplication;
const tokenTimeExpiration = 59;
const headers = {
  'Content-Type':'application/json',
  'Authorization' : `Basic ${authorizationBasic}`
};

@Injectable({
    providedIn: 'root'
})

export class ClientService {

    public auth2: any;
    public usuario: Client
    
  
    constructor( private http: HttpClient, 
                  private router: Router,
                  private ngZone: NgZone ) {
  
    }
  
    crearUsuario( formData: PreRegisterForm ) {
      formData.publicToken = publicToken;
      formData.appUserId   = appUserId;
      formData.ci = ( formData.ci ).toString();
      formData['ciExpedition'] = '';
      return this.http.post(`${ base_url }/api/v1/Administration/PreRegister`, formData, { headers: headers } )
              .pipe(
                tap( (resp: any) => {
                  localStorage.setItem('token', resp.token )
                })
              )
    }

    completarRegistroUsuario( formData: RegisterForm, disabilityString ) {
      const headers = {
        'Content-Type':'application/json',
        'Authorization' : `Bearer ${localStorage.getItem('token') || ''}`
      };
      formData.cellPhone = ( formData.cellPhone ).toString();
      formData['disability'] = formData.disability_condition == 'si' ? true : false;
      formData.passwordAplication = passwordAplication;
      formData.publicToken = publicToken;
      formData.appUserId   = appUserId;
      formData.userAplication = userAplication;
      formData.referencePhone = ( formData.referencePhone ).toString();
      formData['typeDisability'] = disabilityString;
      return this.http.post(`${ base_url }/api/v1/ClientService/RegistrationComplete`, formData, { headers: headers } )
                .pipe(
                  tap( (resp: any) => {
                    // localStorage.setItem('token', resp.token )
                  })
                );
    }
  
    login( formData: LoginForm ) {
      formData['appUserId'] = appUserId;
      formData['publicToken'] = publicToken;
      const httpOptions = {
        headers: new HttpHeaders({
          'Content-Type':  'application/json',
          Authorization: `Base ${authorizationBasic}`,
        }),
          withCredentials: true
      };

      var now = new Date();
      now.setMinutes( now.getMinutes() + tokenTimeExpiration ).toString();
      return this.http.post(`${ base_url }/api/v1/ClientService/Authenticate`, formData, httpOptions )
        .pipe(
          tap( (resp: any) => {
            localStorage.setItem('token', resp['data'].token );
            localStorage.setItem('date_expiration', now.toString()  );
            localStorage.setItem('email',  resp['data'].email );
          }),
        catchError( error => {
          return of(error);
        } )
      );
  
    }

      // Revoke Token no funciona
    logout() {
      var email = localStorage.getItem('email') || '';
      var token = localStorage.getItem('token') || '';
      var headers = {
        'Content-Type':'application/json',
        'Authorization' : `Bearer ${token}`
      };
        // Aumentar refresh token
      var formData = {
        'publicToken'        : publicToken,
        'appUserId'          : appUserId,
        'email'              : email,
        'token'              : 'token',
        'userAplication'     : userAplication,
        'passwordAplication' : passwordAplication
      };
      return this.http.post(`${ base_url }/api/v1/Token/RevokeToken`, formData, { headers: headers } )
      .pipe(
        tap( (resp: any) => {
          localStorage.removeItem( 'email' );
          localStorage.removeItem( 'date_expiration' );
          localStorage.removeItem( 'token' );
        }),
      catchError( error => {
        return of(error);
      } )
    );
    }

    obtenerUsuario() {
      var token = localStorage.getItem('token') || '';
      var email = localStorage.getItem('email') || '';
      var headers = {
        'Content-Type':'application/json',
        'Authorization' : `Bearer ${token}`
      };

      var formData = {
        'publicToken' : publicToken,
        'appUserId'   : appUserId,
        'email'       : email,
        'userAplication' : userAplication,
        'passwordAplication' : passwordAplication
      };
      return this.http.post(`${ base_url }/api/v1/ClientService/MyInformation`, formData, { headers: headers })
      .pipe(
        catchError( error => {
          return of(false);
      } ) )
    }

    refreshToken( dateNow ) {
      var email = localStorage.getItem('email') || '';
      dateNow.setMinutes( dateNow.getMinutes() + tokenTimeExpiration ).toString();
      var headerRefresh = {
        'Content-Type':'application/json',
        'Authorization' : `Basic ${authorizationBasic}`
      };
      var formDataRefresh = {
        'publicToken' : publicToken,
        'appUserId' : appUserId,
        'email' : email
      };
      return this.http.post(`${ base_url }/api/v1/Token/RefreshToken`, formDataRefresh, { headers: headerRefresh } )
      .pipe(
        tap( (resp: any) => {
          localStorage.setItem('token', resp['data'].token );
          localStorage.setItem('date_expiration', dateNow.toString()  );
          localStorage.setItem('email',  resp['data'].email );
        })
      );
    }
    
    obtenerQuiz( numeroQuiz ) {
      var headers = {
        'Content-Type':'application/json',
        'Authorization' : `Bearer ${localStorage.getItem('token') || ''}`
      };

      var formData = {
        'publicToken' : publicToken,
        'appUserId'   : appUserId,
        'moduleNumber'   : numeroQuiz,
        'email'       : localStorage.getItem('email') || '',
        'userAplication' : userAplication,
        'passwordAplication' : passwordAplication
      };
      return this.http.post(`${ base_url }/api/v1/ClientService/QuestionsAndAswers`, formData, {
        headers: headers
      });
    }

    guardarQuiz( numeroQuiz ) {
      var headers = {
        'Content-Type':'application/json',
        'Authorization' : `Bearer ${localStorage.getItem('token') || ''}`
      };
      var formData = {
        'publicToken' : publicToken,
        'appUserId'   : appUserId,
        'userAplication' : userAplication,
        'passwordAplication' : passwordAplication,
        'email'       : localStorage.getItem('email') || '',
        'moduleNumber'   : numeroQuiz
      };
      return this.http.post(`${ base_url }/api/v1/ClientService/AnswerQuestion`, formData, {
        headers: headers
      });
    }

    obtenerUsuarios() {
      var formData = {
        'publicToken' : publicToken,
        'appUserId'   : appUserId
      }
      // /api/v1/Administration/GetWinners
      return this.http.post(`${ base_url }/api/v1/Administration/GetWinners`, formData, {
        headers: headers
      });
    }

    verificarEmail( token ) {
      var formData = {
        'publicToken' : publicToken,
        'appUserId'   : appUserId,
        'tokenEmailVerify': token
      }
      return this.http.post(`${ base_url }/api/v1/Administration/VerifyEmail`, formData, {
        headers: headers
      });
    }

    recuperarCuenta( email ) {
      var formData = {
        'publicToken' : publicToken,
        'appUserId'   : appUserId,
        'email': email
      }
      return this.http.post(`${ base_url }/api/v1/Administration/ForgotPassword`, formData, {
        headers: headers
      });
    }

    cambiarPassword( data, token ) {
      var formData = {
        'publicToken'     : publicToken,
        'appUserId'       : appUserId,
        'token'           : token,
        'email'           : data['email'],
        'password'        : data['password'],
        'confirmPassword' : data['confirmPassword'],
      }
      return this.http.post(`${ base_url }/api/v1/Administration/ResetPassword`, formData, {
        headers: headers
      });
    }

    descargarCertificado( nombre ) {
      var formData = {
        'publicToken'     : publicToken,
        'appUserId'       : appUserId,
        "email"           : localStorage.getItem('email'),
        'certificateParameters' : [
          {
            "label": "",
            "value": nombre,
            "fontSizeLabel": 0,
            "fontSize": 38,
            "verticalTextDirection": 295,
            "horizontalTextDirection": 410
          }
        ]
      }
      return this.http.post(`${ base_url }/api/v1/Administration/SendCertificate`, formData, {
        headers: headers
      });
    }

    refrescarToken() {
      var formData = {
        "publicToken": publicToken,
        "appUserId": appUserId,
        "email": localStorage.getItem('email') || ''
      }
      const httpOptions = {
        headers: new HttpHeaders({
          'Content-Type':  'application/json',
          Authorization: `Basic ${authorizationBasic}`,
        }),
          withCredentials: true
      };
      var now = new Date();
      now.setMinutes( now.getMinutes() + tokenTimeExpiration ).toString();
      return this.http.post(`${ base_url }/api/v1/Token/RefreshToken`, formData, httpOptions)
      .pipe(
        tap( (resp: any) => {
          localStorage.setItem('token', resp['data'].token );
          localStorage.setItem('date_expiration', now.toString());
        }),
        catchError( error => {
          return of(error);
        } )
      );
    }

    revokeToken() {
      var formData = {
        "publicToken"         : publicToken,
        "appUserId"           : appUserId,
        "email"               : localStorage.getItem('email') || '',
        "userAplication"      : userAplication,
        "passwordAplication"  : passwordAplication
      }
      const httpOptions = {
        headers: new HttpHeaders({
          'Content-Type':  'application/json',
          Authorization: `Bearer ${localStorage.getItem('token') || ''}`,
        }),
          withCredentials: true
      };
      return this.http.post(`${ base_url }/api/v1/Token/RevokeToken`, formData, httpOptions);      
    }
}